import { Inter } from "next/font/google";
import "./globals.css";
import { ClerkProvider } from "@clerk/nextjs";
import { Toaster } from "sonner";
import Header from "@/components/header";
import { dark } from "@clerk/themes";
import { ThemeProvider } from "@/components/theme-provider";
import ThemeSwitcher from "@/components/theme-switcher"; // <-- Add this line

const inter = Inter({ subsets: ["latin"] });

export const metadata = {
  title: "Doctors Appointment App",
  description: "Connect with doctors anytime, anywhere",
};

export default function RootLayout({ children }) {
  return (
    <html lang="en" suppressHydrationWarning>
      <head>
        <link rel="icon" href="/logo.png" sizes="any" />
      </head>
      <body className={inter.className}>
        <ThemeProvider attribute="class" defaultTheme="system" enableSystem>
          <ClerkProvider appearance={{ baseTheme: dark }}>
            <Header />
            <ThemeSwitcher /> {/* Theme toggle buttons */}
            <main className="min-h-screen bg-white text-black dark:bg-black dark:text-white">
              {children}
            </main>
            <Toaster richColors />
            <footer className="bg-gray-100 text-gray-800 dark:bg-gray-900 dark:text-gray-200 py-12">
              <div className="container mx-auto px-4 text-center">
                <p>Made with ðŸ’— by Aryan</p>
              </div>
            </footer>
          </ClerkProvider>
        </ThemeProvider>
      </body>
    </html>
  );
}
